<template>
  <div id="shareIt">
    <!-- 在微信环境下弹出分享引导层 -->
     <mt-popup
        v-model="wechatisshow"
        class="bgnone wechat-cotent"
        :modal="true"
        :closeOnClickModal="true"
        @click.native = 'closeShare'
        >
        <div class="" @click.stop="''">
          <h1 class="step1">
            第一步：点击右上角的...
            <img src="../../assets/img/dot.png" class="dot-img" alt="">
          </h1>
          <div class="jiantou">
            <img class="wp100" src="../../assets/img/jiantou.png" alt="">
          </div>
          <div class="imgIcon">
           <h1 class="step2">第二步：选择分享方式进行分享</h1>
           <div class="wechat-icon">
             <div class="wechat-icon-img">
              <img src="../../assets/img/friend.png" class="wp100">
             </div>
             <div class="wechat-icon-img">
              <img src="../../assets/img/wechat.png" class="wp100" alt="">
             </div>
             <div class="wechat-icon-img">
              <img src="../../assets/img/qq.png" class="wp100" alt="">
             </div>
           </div>
          </div>
        </div>
     </mt-popup>
     <!-- 不在微信环境下，但是在APP环境下,引导分享的方式 -->
     <mt-popup
        v-model="appisshow"
        position="bottom"
        class="bgnone"
        :modal="true"
        @click.native='closeShare'
        >
        <div class="app-content" @click.stop='""'>
          <div class="topShareTitle">
            <div class="share_img"></div>
          </div>
          <div class="topshow">
            <div class="icon" @click="setShare('weixin','WXSceneTimeline')">
              <div class="iconBox">
                <div class="iconImg">
                  <img src="../../assets/img/friend.png" alt="">
                </div>
                <div class="name">朋友圈</div>
              </div>
            </div>
             <div class="icon" @click="setShare('weixin','WXSceneSession')">
              <div class="iconBox">
                <div class="iconImg">
                  <img src="../../assets/img/wechat.png" alt="">
                </div>
                <div class="name">微信</div>
              </div>
            </div>
             <div class="icon" v-if="type !== 'jjb_small_video'" @click="setShare('qq')">
              <div class="iconBox">
                <div class="iconImg">
                  <img src="../../assets/img/qq.png" alt="">
                </div>
                <div class="name">QQ</div>
              </div>
            </div>
           
          </div>
          <div class="canlehid" @click="closeShare">取消</div>
        </div>
     </mt-popup>
  </div>
</template>

<script>
// 导入wx 分享接口对象
import wx from 'weixin-js-sdk'
import {mapState} from 'vuex'
// import Clipboard from 'clipboard'
export default {
  name: 'shareIt',
  props: {
    details: {
      type: Object,
      default: null
    },
    type: {
      type: String
    }
  },
  computed: mapState({
    commonState: state => state.common,
    comment: state => state.comment
  }),
  data () {
    return {
      appisshow: false, // 控制在APP环境下要不要分享到朋友圈，微信，QQ
      wechatisshow: false, // 控制微信环境下要不要分享到朋友圈，微信，QQ
      pageUrl: '', // 提交给服务器端的URL
      share: null
    }
  },
  methods: {
    // 分享功能
    /** 实现思路：
     * 1.检测有没有登录，如果没有登录，要先登录，然后再返回到当前页，如果已经登录，检测是否在微信环境
     * 2.如果在微信环境中，直接浮层引导分享，（可分享，微信群，朋友圈，QQ）
     * 3.如果非微信环境，直接将带分享人信息参数的文章分享页短连接，复制到剪贴板上，
     * 格式如下：文章标题前15个字：http://t.cn/fefvDAA 带分享人信息参数，并弹出toast提示："本文链接已经复制，快发送给好友吧"
     * 4.分享后，分享方案和描述如下：标题为文章标题，内容为文章内容，同时最前方加入【掘金宝】标识，图片为文章首图(缺省图片为logo)
     *  */
   
    // 保存分享数据
    setShareParams () {
      let data
      if (this.type === 'jjb_small_video') {
        data = this.details
        data.videoid = data.short_id
        data = {
          data: JSON.stringify(data),
          tag: 'jjb_small_video'
        }
        if (data._id) {
          delete data._id
        }
        // 添加分享次数
        let item = this.comment.currentSmallVideo
        item.statistics.share = item.statistics.share * 1 + 1
        this.$store.commit('comment_get_small_video', item)
        this.$api.post('VIDEO_STORE', data, res => {})
      }
    },
    // 分享功能
    shareComment () {
      let weChatFlag = this.$utils.isWx()
      if (weChatFlag) {
        // 让分享引导浮层弹出来
        this.wechatisshow = true
      } else { // 如果不在微信环境下
        this.appisshow = true
      }
    },
    // 分享朋友圈
    setShare (id, ext) {
      let that = this
      if (localStorage.getItem('domain')) {
        this.$config.currentWebUrl = localStorage.getItem('domain')
      }
      let shareUrl = this.$config.currentWebUrl + '?path=' + this.$route.path + '&inviteCode=' + this.$utils.cache('inviteCode')
      let msg = {
        thumbs: [`https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/aboutus_logo.png?currentTime=${new Date().getTime()}`],
        href: shareUrl
      }
      if (that.type === 'jjb_small_video') {
        msg.title = that.details.desc
        msg.content = that.commonState.userInfo.nickname + '分享了@' + this.details.nickname + '的小视频，赶紧看看吧~'
      } else if (that.type === 'jjb_video') {
        msg.title = that.details.title
        msg.content = that.details.abstract
        msg.thumbs = [that.details.img_url ? this.$utils.exceptWebp(that.details.img_url) : `https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/aboutus_logo.png?currentTime=${new Date().getTime()}`]
      }
      if (ext) {
        msg.extra = {
          scene: ext
        }
      }
      let shareType = 1
      if (id === 'weixin') {
        if (ext === 'WXSceneTimeline') {
          shareType = 2
        } else {
          shareType = 1
        }
        // msg.type = 'web'
      } else {
        shareType = 1
      }
      console.log('分享信息' + JSON.stringify(msg))
      console.log(shareType)
      if (this.share[id].nativeClient) {
        this.share[id].send(msg, function (res) {
          // 微信分享回调参数
          if (id === 'qq') {
            that.$toast({
              message: '分享成功',
              duration: 1500
            })
          }
          that.setShareParams()
          that.closeShare()
        }, function (e) {
          alert(JSON.stringify(e) + '23')
          if (e.code === -2) {
            that.$toast({
              message: '已取消分享',
              duration: 1500
            })
          } else {
            that.$toast({
              message: '分享失败',
              duration: 1500
            })
          }
          that.closeShare()
        })
      } else {
        let msg = ''
        if (id === 'weixin') {
          msg = '微信'
        } else if (id === 'qq') {
          msg = 'qq'
        }
        that.$toast('请先安装' + msg)
      }
    },
    loadWxSignature () {
      let newUrl = ''
      if (location.href.indexOf('#') > -1) {
        newUrl = location.href.split('#')[0]
      } else {
        newUrl = ''
      }
      this.$api.post('WECHAT_SIGNATURE', {url: encodeURIComponent(newUrl), origin_url: location.origin}, (res) => {
        if (res.code === 0) {
          let sig = res.data
          let pageUrl = sig.url + `?path=${this.$route.path}&inviteCode=${this.$utils.cache('inviteCode')}`
          let that = this
          let shareContent = {}
          if (this.type === 'jjb_small_video') {
            shareContent = {
              title: this.details.desc,
              desc: this.commonState.userInfo.nickname + '分享了@' + this.details.nickname + '的小视频',
              link: pageUrl,
              imgUrl: `https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/aboutus_logo.png?currentTime=${new Date().getTime()}`
            }
          } else if (this.type === 'jjb_video') {
            shareContent = {
              title: this.details.title,
              desc: this.details.abstract, // 分享描述
              link: pageUrl, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
              imgUrl: this.details.img_url ? this.details.img_url : `https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/aboutus_logo.png?currentTime=${new Date().getTime()}` // 分享图标
            }
          }
          // 配置微信签名信息
          wx.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: sig.appid, // 必填，公众号的唯一标识
            timestamp: sig.timestamp, // 必填，生成签名的时间戳
            nonceStr: sig.nonceStr, // 必填，生成签名的随机串
            signature: sig.signature, // 必填，签名
            jsApiList: sig.jsApiList // 必填，需要使用的JS接口列表sig.jsApiList
          })
          wx.ready(function () {
            // 分享到QQ空间
            wx.onMenuShareTimeline({
              title: shareContent.title, // 分享标题
              desc: shareContent.desc, // 分享描述
              link: shareContent.link, // 
              imgUrl: shareContent.imgUrl, // 分享图标
              success: function (res) {
                that.closeShare()
                that.setShareParams()
              }
            })
            wx.onMenuShareAppMessage({
              title: shareContent.title, // 分享标题
              desc: shareContent.desc, // 分享描述
              link: shareContent.link, // 
              imgUrl: shareContent.imgUrl, // 分享图标
              success: function () {
                that.closeShare()
                that.setShareParams()
              }
            })
            wx.onMenuShareQQ({
              title: shareContent.title, // 分享标题
              desc: shareContent.desc, // 分享描述
              link: shareContent.link, // 
              imgUrl: shareContent.imgUrl, // 分享图标
              success: function () {
                that.setShareParams()
                that.closeShare()
              },
              cancel: function () {
                that.closeShare()
              }
            })
            wx.onMenuShareWeibo({
              title: shareContent.title, // 分享标题
              desc: shareContent.desc, // 分享描述
              link: shareContent.link, // 
              imgUrl: shareContent.imgUrl// 分享图标
            }, function (res) {
              that.setShareParams()
              that.closeShare()
            })
          })
          wx.error((res) => {
             
          })
        }
      })
    },
    closeShare () {
      this.$emit('closeShare')
      this.wechatisshow = false
      this.appisshow = false
    }
  },
  watch: {
    'appisshow': function (flag) {
      if (!flag) {
        this.closeShare()
      }
    },
    $route: function () {
      // this.loadWxSignature()
    }
  },
  mounted () {
    const that = this
    this.shareComment() // 调用分享功能
    this.loadWxSignature()
    var setAppShare = function () {
      if (that.$config.source_style === 6 || that.$config.source_style === 7) {
        that.share = {}
        window.plus.share.getServices(function (s) {
          for (let v of s) {
            that.share[v.id] = v
          }
        })
      }
    }
    if (window.plus) {
      setAppShare()
    } else {
      document.addEventListener('plusready', function () {
        setAppShare()
      })
    }
  }
}

</script>

<style lang="scss" scoped>
#shareIt{
  width: 0;
  height: 0;
  .app-content{
    height: 422px;
    position: relative;
    border: 0;
    width: 750px;
    z-index: 1;
    filter:alpha(opacity=30);
    .topShareTitle{
      height: 100px;
      line-height: 100px;
      color: #fff;
      font-size: 34px;
      text-align: center;
      background: linear-gradient(to right,#fcb164, #f65635);
      position: relative;
      .share_img{
        height: 150px;
        background: url('../../assets/img/share_title.png') no-repeat;
        background-size: 750px 150px;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
      }
    }
    .topshow{
      border:1px solid #ddd;
      background: #dbdbdb;
      padding: 0 115px;
      height: 230px;
      overflow: hidden;
      position: relative;
      text-align: center;
      align-items: center;
      display: flex;
      justify-content: space-around;
      .iconImg{
        width: 110px;
        height: 110px;
        img{
          width: 100%;
          height: 100%;
        }
      }
      .name{
        margin-top: 15px;
        font-size: 20px;
      }

    }
    .canlehid{
      height: 90px;
      border-top:1px solid #dbdbdb;
      box-sizing: border-box;
      background: #fff;
      text-align: center;
      bottom: 0;
      position: absolute;
      width: 100%;
      line-height: 80px;
    }
  }
}
.wechat-cotent{
  width: 100%;
  height: 100%;
  color: #fff;
  font-size: 34px;
  .jiantou{
    width: 206px;
    position: absolute;
    right: 30px;
    top:20px;
  }
  .wechat-icon-img {
    width: 110px;
    height: 110px;
  }
  .step1{
    padding:123px 0 75px 60px;
    display: flex;
    align-items: center;
    align-content: center;
    .dot-img{
      width: 70px;
      margin-left: 20px;
    }
  }
  .step2{
    padding-left: 110px;
    padding-bottom: 60px;
  }
  .wechat-icon{
    width: 484px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
}
</style>



