<template>
  <div id="smallVideoComment" ref="smallVideoComment">
    <div class="videolist" v-for="(item, index) in list" :key="index">
      <div class="videoItem">
        <div class="left-content">
          <div class="left-icon">
            <img :src="'https:' + item.avatar" alt="头像">
          </div>
        </div>
        <div class="right-content">
          <div class="userInfo">
            <div class="nickname" v-html="item.nickname"></div>
          </div>
          <div class="right_detail twoLine" v-html="item.text"></div>
          <div class="right_time">
            <div class="releaseTime">{{$utils.timeFormat(item.create_time*1000)}}</div>
          </div>
          <div class="rightdigg" :class="{isDigg: item.is_digg}">
            <div class="digg-icon">
              <img v-if="item.is_digg" src="../../assets/img/index/digg_active.png" alt="">
              <img v-else @click="setDigg(item, index)" src="../../assets/img/index/digg.png" alt="">
            </div>
            <div class="text">{{item.zan | setNumber}}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="tips">
      <span v-if="isApp">{{tipText}}</span>
      <a v-else :href="jumpLink">{{tipText}}</a>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    list: {
      type: Array
    }
  },
  filters: {
    // 时间过滤器
    formatDate: (millisecond) => {
      let times = (new Date().getTime()) / 1000 - millisecond
      let date = new Date(parseInt(millisecond * 1000))
      if (times <= 60) {
        return '刚刚'
      } else if (times > 60 && times <= 3600) {
        return parseInt(times / 60) + '分钟前'
      } else if (times > 3600 && times <= 86400) {
        return parseInt(times / 3600) + '小时前'
      } else if (times > 86400 && times <= 172800) {
        return '昨天 ' + (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':' + (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes())
      } else if (times > 172800) {
        return date.getFullYear() + '年' + (date.getMonth() + 1) + '月' + date.getDate() + '日' + date.getHours() + ':' + date.getMinutes()
      }
    }
  },
  data () {
    return {
      jumpLink: '',
      tipText: '',
      isApp: false
    }
  },
  methods: {
    setDigg (item, index) {
      this.$emit('fatherDigg', {
        item,
        index
      })
    }
  },
  beforeMount () {
    if (this.$config.source_style === 6 || this.$config.source_style === 7) {
      this.jumpLink = 'javascript:;'
      this.isApp = true
      this.tipText = '精彩评论等你发布~'
    } else {
      this.tipText = '到掘金宝app查看更多评论'
      this.isApp = false
      this.jumpLink = 'http://sj.qq.com/myapp/detail.htm?apkName=com.juejinchain.android'
    }
  },
  mounted () {
    
  }
}
</script>
<style lang="scss" scoped>
.videolist{
  padding: 10px;
  .videoItem{
    padding: 14px 20px 14px;
    display: flex;
    // border-bottom: 1px solid #fafafa;
    .left-content{
      width: 96px;
      .left-icon{
        width: 76px;
        height: 76px;
        overflow: hidden;
        border-radius: 50%;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
    .right-content {
      min-width: 0;
      flex:1;
      font-size: 30px;
      position: relative;
      .userInfo{
        display: flex;
        justify-content: space-between;
        width: 100%;
        height: 60px;
        align-items: center;
        color: #999;
        .nickname {
          font-size: 28px;
          line-height: 40px;
          color: #4A6F99;
          margin-bottom: 15px;
        }
      }
      .rightdigg{
        display: flex;
        align-items: flex-end;
        position: absolute;
        right: 0;
        top: 0;
        color: #8698AE;
        font-size: 28px;
        .digg-icon{
          width: 30px;
          height: 30px;
          margin-right: 10px;
          img{
            width: 100%;
            height: 100%;
          }
        }
      }
      .rightdigg.isDigg {
        color: #ed8778;
      }
      .right_detail{
        line-height: 45px;
        font-size: 32px;
      }
      .right_time{
        padding: 10px 0;
        font-size: 26px;
        color: #333;
      }
      
    }
  }
}
.tips{
  text-align: center;
  height: 80px;
  line-height: 80px;
  padding-bottom: 10px;
  font-size: 30px;
  span{
    color: #999
  }
  a {
    color:blue;
    text-decoration: none
  }
  a:hover{
    color:blue;
  }
  a:active{
    color:blue;
  }
  a:link{
    color:blue;
  }
}
</style>



