<template>
  <div class="v-tocuh" ref="vTouch" v-if="comment.currentSmallVideo.statistics">
    <div id="small-video-detail" v-if="!isBrowser">
      <!-- <v-touch class="vTouch" v-on:swipeleft="swipeLeft" v-on:swiperight="swiperight" v-on:swipeup="swipeup" v-on:swipedown="swipedown"> -->
      <div id="small-video" ref='smallVideo' @click="setPlay">
        <video :src="'http:' + this.comment.currentSmallVideo.video_url" :poster="'http:' + $utils.exceptWebp(this.comment.currentSmallVideo.video_img)" loop autoplay></video>
      </div>
      <!-- </v-touch> -->
      <div class="playBtn" v-if="!isPlay" @click.stop="setPlayTrue">
        <img style="color:#555" src="../../assets/img/play.png" alt="">
      </div>
      <div class="gradientbg" ref="gradientbg" @click="setPlay"></div>
      <div class="small-absolute rightStatic">
        <div class="small-digg rightIcon" :class="{isDigg:isDigg}">
          <div class="icon" @click="setDigg">
            <img src="../../assets/img/digg_active.png" v-if="isDigg" alt="点赞">
            <img src="../../assets/img/digg.png" v-else alt="点赞">
          </div>
          <div class="digg-icon" v-if="showDigg" :class="{active: isDigg}">
            <img src="../../assets/img/zan_move.png" alt="">
          </div>
          <div class="samll-text">{{comment.currentSmallVideo.statistics.zan | setNumber}}</div>
        </div>
        <div class="small-comment rightIcon">
          <div class="icon" @click="swipeup">
            <img src="../../assets/img/comment.png" alt="评论">
          </div>
          <div class="samll-text">{{commentList.length}}</div>
        </div>
        <div class="small-share rightIcon" v-if="showShare">
          <div class="icon" >
            <img @click="configShare" src="../../assets/img/Share.png" alt="分享">
          </div>
          <div class="samll-text">{{comment.currentSmallVideo.statistics.share | setNumber}}</div>
        </div>
      </div>
      <!-- 图文介绍与媒体 -->
      <div class="smallMedia">
        <div class="s-m-detail fourLine">{{comment.currentSmallVideo.desc}}</div>
        <div class="mediaInfo">
          <div class="media-icon">
            <img :src="'https:' + comment.currentSmallVideo.avatar" alt="媒体头像">
          </div>
          <div class="media-name" :title="comment.currentSmallVideo.nickname">{{comment.currentSmallVideo.nickname}}</div>
        </div>
      </div>
      <!-- 图文介绍与媒体结束 -->
      <!-- 评论 -->
      <div class="comment commentBlur" :class="{showInput:!showInput}">
        <div class="backIcon" @click="backToIndex()">
          <img src="../../assets/img/back.png" alt="返回">
        </div>
        <div class="commentClick" for="showRelease"  @click="setShowInput">
          <div class="editIcon">
            <img src="../../assets/img/editIcon.png" alt="">
          </div>
          <span class="text">写评论...</span>
        </div>
      </div>
      <!-- 评论结束 -->
      <!-- 评论列表弹框 -->
      <div class="commentListBox" :class="{showReply:showReply}"  >
        <div class="commentOut" @click="showReply = false"></div>
        <div class="commentList" >
          <div class="commmentBox" >
            <div class="comment-list-title">
              <!-- <div v-if="commentList.length > 0"><span>{{commentList.length}}条精彩热评</span>&nbsp;&nbsp;&nbsp;<span>（共{{comment.currentSmallVideo.statistics.comment | setNumber}}条）</span></div>
              <div v-else><span>{{commentList.length}}条精彩热评</span>&nbsp;&nbsp;&nbsp;<span>（共{{commentList.length}}条）</span></div> -->
              <div>
                <span>下方评论（{{commentList.length}}）条</span>
              </div>
              <div class="closeIcon" @click="showReply = false">
                <img src="../../assets/img/login_close.png" alt="">
              </div>
            </div>
          </div>
          <div class="centerComment" @touchmove.stop='touchMove'>
            <!-- 有评语 -->
            <div class="c-comment" ref="commmentList" v-if="commentList.length > 0">
              <small-comment v-if="commentList.length > 0" @fatherDigg='childSetDigg' :list="commentList"></small-comment>
            </div>
            <!-- 无评语 -->
            <div class="c-comment no-comment" v-else>
              <div class="no-comment-icon">
                <img src="../../assets/img/no-comment.png" alt="">
              </div>
              <div class="no-comment-text">等你发表精彩评论～</div>
            </div>
          </div>
          <div class="bottomComment">
            <div class="comment commentBlur" :class="{showInput:!showInput}">
              <div class="commentClick" for="showRelease"  @click="setShowInput">
                <div class="editIcon">
                  <img src="../../assets/img/editIconGray.png" alt="">
                </div>
                <span class="text">写评论...</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 输入评语实际输入框 -->
      <div class='commonentInput' v-if="showRelease" @click="showRelease = false">
        <div class="commentTextarea" @click.stop="">
          <div class="commentTextBox">
            <textarea id="showRelease" @keyup.enter='release' class="commentText" ref="showRelease" placeholder="优质评论将会优先展示" v-model="content" maxlength="100" rowspans='3' colspans='4'></textarea>
          </div>
          <div class="release"><div class="send" @click="release">发送</div></div>
        </div>
      </div>
      <!-- 输入评语结束 -->
      <!-- v1/video/save  详情保存  参数 data(json):必须包含videoid   tag(string):jjb_video或者jjb_small_video
        v1/video/detail 详情查看  参数data(json):必须包含videoid   tag(string):jjb_video或者jjb_small_video -->
    </div>
    <div v-else class="videoContent" ref='smallVideo'>
      <div ref="gradientbg" class="gradientbg" @click="setPlay" :style="{backgroundImage: 'http' + $utils.exceptWebp(this.comment.currentSmallVideo.video_img)}">
        <video :src="'http:' + this.comment.currentSmallVideo.video_url" v-show="!commonState.login_box" controlsList="nodownload" :poster="'http' + $utils.exceptWebp(this.comment.currentSmallVideo.video_img)" loop autoplay></video>
        <div class="playBtn" v-if="!isPlay" @click.stop="setPlayTrue">
          <img style="color:#555" src="../../assets/img/play.png" alt="">
        </div>
      </div>
      <div class="video-news">{{comment.currentSmallVideo.desc}}</div>
      <div class="bottomButton">
        <div class="backButton" @click="backToIndex()">
          <div class="iconSize back">
            <img src="../../assets/img/details/exit.png" alt="返回">
          </div>
        </div>
        <div class="backButton" @click="next(1)">
          <div class="iconSize pre">
            <img src="../../assets/img/details/left.png" alt="上一步">
          </div>
        </div>
        <div class="backButton" @click="next(2)">
           <div class="iconSize pre">
            <img src="../../assets/img/details/right.png" alt="下一步">
          </div>
        </div>
        <div class="backButton">
          <div>
            <div class="small-digg rightIcon" :class="{isDigg:isDigg}">
              <div class="icon" @click="setDigg">
                <img src="../../assets/img/details/digg_active.png" v-if="isDigg" alt="点赞">
                <img src="../../assets/img/details/digg.png" v-else alt="点赞">
              </div>
              <div class="digg-icon" v-if="showDigg" :class="{active: isDigg}">
                <img src="../../assets/img/zan_move.png" alt="">
              </div>
              <div class="samll-text">{{comment.currentSmallVideo.statistics.zan | setNumber}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <shareIt v-if="shareBol" :details='comment.currentSmallVideo' :type='"jjb_small_video"' @closeShare='closeShare'></shareIt>
    <prompt-box
      :dropBol="showNetTip"
      @leftBtn="showNetTip=false"
      @rightBtn="confirmPlay"
      rightTxt="确定"
      contentTxt="您正在使用流量播放，是否继续播放">
    </prompt-box>
    <articleMd v-if="isShow" :coinParams='coinParams'></articleMd>
    <swipe-guild v-if="showSwipeGuild" @click.native="hideGuild"></swipe-guild>
  </div>
</template>
<script>
import {mapState, mapMutations} from 'vuex'
import PromptBox from '@/components/common/prompt-box'
import smallComment from '@/components/indexDetail/small-video-comment'
import shareIt from '@/components/indexDetail/share-it'
import articleMd from '../common/article-md'
import SwipeGuild from './swipe-guild'
import wx from 'weixin-js-sdk'
// import '../../assets/js/touch.js'
// import PearPlayer from 'pearplayer'
export default {
  components: {
    smallComment,
    shareIt,
    PromptBox,
    articleMd,
    SwipeGuild
  },
  data () {
    this.isBrowser = this.$utils.isSpecialBrowser() || this.$config.source_style === 6 || this.$utils.isIos()
    return {
      showInput: false,
      showShare: false,
      isPlay: true,
      isDigg: false,
      showReply: false,
      showRelease: false,
      configText: {},
      showDigg: true,
      shareBol: false,
      content: '',
      networkType: 'wifi',
      showNetTip: false,
      isShow: false,         // 首次阅读显示
      selfComment: [],
      commentList: [],
      showSwipeGuild: false
    }
  },

  computed: mapState({
    comment: state => state.comment,
    commonState: state => state.common
  }),
  // beforeR
  watch: {
    $route: function () {
      this.selfComment = []
      this.selfComment = []
      this.commentList = [...this.selfComment, ...this.$store.state.comment.currentSmallVideo.comments.sort(this.$utils.compare('zan'))]
      this.showDigg = true
      this.isDigg = false
      this.loadWx()
      this.initVideo()
      this.initData()
      this.timestamp = this.$utils.getTimeStamp()
      this.$utils.cache('rewardId', this.$route.params.id)
    }
  },
  methods: {
    initData () {
      this.setReward()
    },
    // 调取获得文章奖励的接口
    getReward () {
      if (this.$utils.isGetReard(this.$route.params.aid)) {
        this.$api.post('ARCREWARD_TIP', {aid: this.$route.params.aid, type: 'svideo'}, res => {
          if (res.data.coin > 0) {
            if (!this.$utils.cache('first_tips')) {
              this.coinParams = Object.assign({type: 'svideo'}, res.data)
              this.$utils.cache('first_tips', 1)
              this.isShow = true
            } else if (this.$config.source_style >= 6) {
              let shareSuccessObj = Object.assign({type: 1, tipText: res.data.extra_coin ? `用掘金宝App看视频额外多赚了${res.data.extra_coin}金币` : ''}, res.data)
              if (!this.$utils.cache('isFirstRead')) {
                this.set_shareSuccessObj({
                  type: 16,
                  shareSuccessObj
                })
                this.set_shareType(16)
                this.$utils.cache('isFirstRead', 1)
              } else {
                this.set_shareSuccessObj({
                  type: 7,
                  shareSuccessObj
                })
                this.set_shareType(7)
              }
              this.setShowShare(true)
            } else {
              let shareSuccessObj = Object.assign({type: 1, tipText: `用掘金宝App看视频赚更多金币`}, res.data)
              this.set_shareSuccessObj({
                type: 7,
                shareSuccessObj
              })
              this.set_shareType(7)
              this.setShowShare(true)
            }
            // 已获得奖励设置无奖励
            this.$utils.setArticle(this.$route.params.aid, 'svideo')
          }
        })
      }
    },
    // 设置奖励
    setReward () {
      this.count = 0
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
      this.timer = setInterval(() => {
        this.count++
        if (this.count >= 10) {
          this.getReward()
          clearInterval(this.timer)
          this.timer = null
        }
      }, 1000)
    },
    initVideo () {
      if (this.smallVideo) {
        this.smallVideo.destroy()
      }
      this.video = this.$refs.smallVideo.getElementsByTagName('video')[0]
      if (this.isBrowser || this.$config.source_style === 6) {
        this.video.setAttribute('controlsList', 'nodownload')
        this.video.removeAttribute('controls', 'controls')
        // this.video.setAttribute('x5-video-player-type', 'h5')
        this.video.setAttribute('x5-video-player-fullscreen', true)
        this.video.setAttribute('x5-video-player-fullscreen', 'portrait')
        this.video.setAttribute('preload', 'auto')
        if (this.$utils.isIos()) {
          this.video.setAttribute('webkit-playsinlin', 'portrait')
          this.video.setAttribute('playsinline', 'true')
          this.video.setAttribute('x-webkit-airplay', 'allow')
        }
        this.video.load()
        this.video.play()
      } else {
        this.video.setAttribute('x5-video-player-type', 'h5')
        this.video.setAttribute('x5-video-player-fullscreen', true)
        this.video.setAttribute('x5-video-player-fullscreen', 'portrait')
      }
      this.$refs.gradientbg.addEventListener('swipeleft', this.swipeLeft)
      this.$refs.gradientbg.addEventListener('swiperight', this.swipeRight)
      this.$refs.gradientbg.addEventListener('swipeup', this.swipeup)
      this.$refs.gradientbg.addEventListener('swipedown', this.swipedown)
      this.video.addEventListener('swipeleft', this.swipeLeft)
      this.video.addEventListener('swiperight', this.swipeRight)
      this.video.addEventListener('swipeup', this.swipeup)
      this.video.addEventListener('swipedown', this.swipedown)
      if (!this.$utils.cache('showNetWorkTip') && (this.$utils.getNetworkType() === '4g' || this.$utils.getNetworkType() === '3g')) {
        this.showNetTip = true
        return 0
      } else {
        this.video.load()
        this.video.play()
        this.isPlay = true
      }
      // this.smallVideo.on('canplay', () => {
      //   this.video = this.$refs.smallVideo.getElementsByTagName('video')[0]
      //   this.video.setAttribute('x5-video-player-type', 'h5')
      //   this.video.setAttribute('x5-video-player-fullscreen', true)
      //   this.video.setAttribute('x5-video-player-fullscreen', 'portrait')
      //   this.video.style = 'object-fit: scale-down'
      //   if (!this.$utils.cache('showNetWorkTip') && (this.$utils.getNetworkType() === '4g' || this.$utils.getNetworkType() === '3g')) {
      //     this.showNetTip = true
      //     return 0
      //   } else {
      //     this.smallVideo.play()
      //     this.isPlay = true
      //   }
      // })
    },
    ...mapMutations([
      'comment_get_small_video',
      'comment_get_small_videolist',
      'comment_set_small_index',
      'common_login_box',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType'
    ]),
    // 禁止滑动事件冒泡
    touchMove (e) {
      e.preventDefault()
    },
    confirmPlay () {
      this.$utils.cache('showNetWorkTip', 1)
      this.showNetTip = false
      this.initVideo()
    },
    setPlay (e) {
      if (!this.$utils.cache('showNetWorkTip')) {
        this.showNetTip = true
        setTimeout(() => {
          this.isPlay = false
        }, 3000)
        return 0
      }
      if (this.isPlay) {
        this.video.pause()
        setTimeout(() => {
          this.isPlay = false
        }, 400)
      } else {
        this.video.play()
        this.isPlay = true
      }
    },
    setPlayTrue () {
      if (!this.$utils.cache('showNetWorkTip')) {
        this.showNetTip = true
        this.isPlay = false
        return 0
      }
      this.video.play()
      this.isPlay = true
    },
    // 关闭分享组件
    closeShare () {
      this.shareBol = false
    },
    // 点击分享按钮
    configShare () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      this.shareBol = true
    },
    // 分享回调提交分享的内容
    setShareParams () {
      let data = this.comment.currentSmallVideo
      data.videoid = data.short_id
      if (!data.videoid) return
      if (data._id) {
        delete data._id
      }
      data = {
        data: JSON.stringify(data),
        tag: 'jjb_small_video'
      }
      this.$api.post('VIDEO_STORE', data, res => {
      })
    },
    loadWx () {
      // 配置签名时保存分享信息
      this.setShareParams()
      let newUrl = ''
      if (location.href.indexOf('#') > -1) {
        newUrl = location.href.split('#')[0]
      } else {
        newUrl = ''
      }
      this.$api.post('WECHAT_SIGNATURE', {url: encodeURIComponent(newUrl), origin_url: location.origin}, (res) => {
        if (res.code === 0) {
          let sig = res.data
          this.pageUrl = sig.url
          let that = this
          let shareContent = {
            title: this.comment.currentSmallVideo.desc,
            desc: this.commonState.userInfo.nickname + '分享了@' + this.comment.currentSmallVideo.nickname + '的小视频', // 分享描述
            link: location.href.indexOf('#') > -1 ? sig.url + `?inviteCode=${this.$utils.cache('inviteCode')}&path=${this.$route.path}` : sig.url, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
            imgUrl: `https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/aboutus_logo.png?currentTime=${new Date().getTime()}` // 分享图标
          }
          // 配置微信签名信息
          wx.config({
            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
            appId: sig.appid, // 必填，公众号的唯一标识
            timestamp: sig.timestamp, // 必填，生成签名的时间戳
            nonceStr: sig.nonceStr, // 必填，生成签名的随机串
            signature: sig.signature, // 必填，签名
            jsApiList: sig.jsApiList // 必填，需要使用的JS接口列表sig.jsApiList
          })
          wx.ready(function () {
            // 分享到朋友圈
            wx.onMenuShareTimeline({
              title: shareContent.title, // 分享标题
              desc: shareContent.desc, // 分享描述
              link: shareContent.link, //
              imgUrl: shareContent.imgUrl, // 分享图标
              success: function () {
                that.setShareParams()
              }
            })
            wx.onMenuShareAppMessage({
              title: shareContent.title, // 分享标题
              desc: shareContent.desc, // 分享描述
              link: shareContent.link, //
              imgUrl: shareContent.imgUrl, // 分享图标
              success: function () {
                that.setShareParams()
              }
            })
            wx.onMenuShareQQ({
              title: shareContent.title, // 分享标题
              desc: shareContent.desc, // 分享描述
              link: shareContent.link, //
              imgUrl: shareContent.imgUrl, // 分享图标
              success: function () {
                that.setShareParams()
              }
            })
          })
          wx.error((res) => {

          })
        }
      })
    },
    // 设置点赞
    setDigg () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      } else {
        this.isDigg = true
        setTimeout(() => {
          this.showDigg = false
        }, 500)
      }
    },
    // 子组件设置元素
    childSetDigg ({item, index}) {
      item.is_digg = true
      item.zan = item.zan * 1 + 1
      let obj = this.comment.currentSmallVideo
      obj.comments[index] = item
      this.comment_get_small_video(obj)
    },
    // 发布评论
    release () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      if (!this.content.trim()) {
        // 空的话不做任何反应
        this.$toast({
          message: '请填写评论后发布',
          position: 'center',
          duration: 1500
        })
      } else if (this.content.length > 100) {
        this.$toast({
          message: '对不起，最多只能写100个字',
          position: 'bottom',
          duration: 1500
        })
      } else {
        this.commentList = [{
          avatar: this.commonState.userInfo.avatar.substr(this.commonState.userInfo.avatar.indexOf(':') + 1, this.commonState.userInfo.avatar.length),
          create_time: parseInt(new Date().getTime() / 1000),
          nickname: this.commonState.userInfo.nickname,
          text: this.content.trim(),
          zan: 0
        }, ...this.commentList]
        this.content = ''
        this.$toast({
          message: '评论成功',
          duration: 1500
        })
        let obj = this.comment.currentSmallVideo
        obj.statistics.zan = obj.statistics.zan * 1 + 1
        this.comment_get_small_video(obj)
        this.showReply = true
        this.$nextTick(() => {
          this.$refs.commmentList.scrollTop = 0
          this.showRelease = false
          this.$refs.showRelease.blur()
        })
      }
    },
    // 显示发布评论框
    setShowInput () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      this.showRelease = true
      this.$refs.vTouch.scrollTo(0, 1000)
      setTimeout(() => {
        this.$refs.showRelease.focus()
      }, 300)
    },
    // 回到首页
    backToIndex () {
      this.$router.push('/')
    },
    next (e) {
      if (e === 1) {
        this.swipeRight()
      } else if (e === 2) {
        this.swipeLeft()
      }
    },
    // 从右往左滑动
    swipeLeft () {
      let index = this.comment.smllVideoIndex * 1 + 1
      if (index >= this.comment.smallVideoList.length) {
        this.$router.replace('/')
      } else {
        let item = this.comment.smallVideoList[index]
        this.comment_get_small_video(item)
        this.comment_set_small_index(index)
        this.$router.replace(`/samllvideodetail/${item.short_id}`)
      }
    },
    // 从左往右滑动
    swipeRight () {
      this.$refs.vTouch.scrollTo(0, 1000)
      let index = this.comment.smllVideoIndex - 1
      if (index >= 0) {
        let item = this.comment.smallVideoList[index]
        this.comment_get_small_video(item)
        this.comment_set_small_index(index)
        this.$router.replace(`/samllvideodetail/${item.short_id}`)
      } else {
        this.$router.replace('/')
      }
    },
    swipeup () {
      this.showReply = true
    },
    swipedown (e) {
      if (this.showReply) {
        this.showReply = false
      } else {
        this.$router.push('/')
      }
    },
    getDetail () {
      let data = {
        videoid: this.$route.params.aid,
        tag: 'jjb_small_video'
      }
      this.$api.post('CHECK_VIDEOSTORE', data, res => {
        if (res.data) {
          this.comment_get_small_video(res.data)
          this.setShareParams()
          this.initVideo()
        } else {
          this.$router.replace('/notFound')
        }
      }, res => {
        this.$router.replace('/')
      })
    },
    hideGuild () {
      this.$utils.cache('swipeGuild', this.$utils.getToadymSec())
      this.showSwipeGuild = false
    }
  },
  beforeMount () {
    if (this.$config.source_style === 6 || this.$config.source_style === 7) {
      this.showShare = true
    } else if (this.$utils.isWx()) {
      this.showShare = true
    } else {
      this.showShare = false
    }
  },
  mounted () {
    let guild = this.$utils.cache('swipeGuild')
    if (!guild) {
      this.showSwipeGuild = true
    } else {
      this.showSwipeGuild = false
    }
    this.loadWx()
    if (this.$route.query.inviteCode) {
      console.log(this.$route.query.inviteCode)
      this.getDetail()
    } else {
      if (!this.comment.currentSmallVideo.videoid) {
        this.$router.replace('/notFound')
      }
    }
    this.timestamp = this.$utils.getTimeStamp()
    this.$utils.cache('rewardId', this.$route.params.id)
    this.initVideo()
  }
}
</script>

